@import url('https://fonts.googleapis.com/css?family=Questrial|Roboto');

$primary: #5764c6;
$secondary: #eff1fa;
$dark: #454d5d;
$gray-dark: #727e96;
$gray: #acb3c2;
$gray-light: #e7e9ed;
$border: #f0f1f4;
$background: #f8f9fa;
$light: #ffffff;
$success: #32b643;
$warning: #ffb700;
$error: #e85600;

body {
  font-family: 'Roboto', sans-serif !important;
  background-color: $light;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Questrial', sans-serif !important;
}

.alchemy.root {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

.alchemy.header {
  .ui.menu .item img.logo {
    margin-right: 0;
    width: 7.4em;
  }
}

.alchemy.content {
  margin-top: 3.14em;
  flex: 1;
}

.alchemy.footer {
  margin-top: 3.14em;
  .copyright {
    color: $gray-dark;
  }
}

#home {
  background-color: $primary;
  min-height: 42em;
  min-width: 100vw;
  display: flex;
  align-items: center;
  position: relative;
  #particles-js {
    position: absolute;
    width: 100%;
    height: 100%;
  }
  .ui.container {
    z-index: 1;
    .ui.header {
      font-size: 4.6rem;
      color: #fff;
      .sub.header {
        color: #fff;
        font-size: 1.6rem;
      }
    }
    .ui.button {
      margin-top: 2em;
      width: 16rem;
      color: #fff;
    }
  }
}

#about {
  background-color: #1c2ba1;
  min-height: 42em;
  min-width: 100vw;
  padding-top: 4rem;
  .column {
    margin: auto;
    .ui.header {
      color: $light;
      font-size: 3rem;
    }
    p {
      color: $light;
      text-align: justify;
    }
  }
}

#feature1, #feature2, #feature3, #feature4, #feature5, #feature6, #feature7, #feature8, #feature9 {
  min-height: 20em;
  min-width: 100vw;
  display: flex;
  align-items: center;
  .column {
    margin: auto;
    .ui.header {
      color: $light;
      font-size: 3rem;
    }
    p {
      color: $light;
    }
  }
}

#feature1 { background-color: #38465d; }
#feature2 { background-color: #c17638; }
#feature3 { background-color: #bbbba4; }
#feature4 { background-color: #38465d; }
#feature5 { background-color: #4ba18b; }
#feature6 { background-color: #77b3d4; }

#enterprise {
  background-color: #1c2ba1;
  min-height: 42em;
  min-width: 100vw;
  padding-top: 4rem;
  .column {
    margin: auto;
    .ui.header {
      color: $light;
      text-align: center;
    }
  }
}

#consumers {
  background-color: #5764c6;
  min-height: 42em;
  min-width: 100vw;
  padding-top: 4rem;
  .ui.header {
    color: $light;
    text-align: center;
    font-size: 3rem;
  }
}

#topup {
  background-color: #5764c6;
  padding-top: 4rem;
  padding-bottom: 4rem;
}

#get-identity {
  background-color: $secondary;
  min-height: 42em;
  min-width: 100vw;
  padding-top: 4rem;
  .ui.header {
    color: $gray-dark;
    text-align: center;
    font-size: 3rem;
  }
}

#meet-sophia {
  background-color: $light;
  min-height: 42em;
  min-width: 100vw;
  padding-top: 4rem;
  padding-bottom: 4rem;
  .column {
    margin: auto;
    .ui.header {
      color: $gray-dark;
      font-size: 3rem;
    }
    .ui.image {
      padding-left: 0;
      padding-right: 0;
      box-shadow: 0px 5px 17px 1px rgba(0,0,0,0.75);
    }
  }
}

#investors {
  background-color: $background;
  min-height: 20em;
  min-width: 100vw;
  padding-top: 4rem;
  .ui.header {
    color: $gray-dark;
    text-align: center;
    font-size: 3rem;
  }
  .swap {
    width: 50%;
  }
  .swap img:last-child {
    display: none;
  }
  .swap:hover img:first-child {
    display: none;
  }
  .swap:hover img:last-child {
    display: block;
  }
}

#privacy, #terms {
  margin-top: 3.14em;
}

